.flexBetween {
    display: flex;
    justify-content: space-between;

    .admin & {
        ul {
            padding: 0;
            margin: 0;

            li {
                display: inline-block;
                margin-left: 10px;

                &:first-child {
                    margin-left: 0;
                }

                a {
                    display: block;
                    padding: 5px 15px;
                    border-radius: 5px;
                    background: #cbcbcb;
                    border: 2px solid #cbcbcb;
                    @include tr;

                    &:hover,
                    &:focus,
                    &:active {
                        background: #d5d5d5;
                    }
                }
            }
        }
    }
}



.liste_refuge {
    .item {
        padding: 0 !important;
        border: 2px solid #cdcdcd;

        & > div {
            height: 100%;
            display: flex;
            align-items: center;

            h3 {
                margin-bottom: 0;
            }
        }
    }
}

.add_item {
    .itemInfo {
        height: 100%;
        background: #f1f1f1;

        a {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 100%;

            &::after {
                content: fa-content($fa-var-circle-plus);
                @extend .fas;
                font-size: 4.5rem;
            }
        }
    }
}

.searchBar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 50px;
    box-sizing: border-box;
    margin-bottom: 40px;
    background: #e1e1e1;
    border: 2px solid #cfcfcf;
    border-radius: 5px;

    input {
        flex: 0 0 calc(100% - 180px);
        padding: 10px;
        border-radius: 5px;
        border: 1px solid #b7b7b7;
    }
}

.refuge_style {
    .voirPlus {
        background: #ef3842;
        border: 2px solid #ef3842;
    }
}

.user_style {
    .voirPlus {
        background: #3846ef;
        border: 2px solid #3846ef;
    }
}

.section-hidden {
    padding: 30px 40px;
    border-bottom: 2px solid #252525;
    margin-bottom: 50px;

    &:last-child {
        border: 0;
    }
}